<template>

  
  <div class="yhaha">
    <title>Katik</title>
    <!-- Header/ judul website start-->
    <header>
      <h1>Katik <span>Kalkulator Matematik</span></h1>
    </header>
    <!-- Header/ judul website end-->

    <!-- Halaman web start-->
    <main>
      <div class="text">
        <h2>Welcome to Katik</h2>
        <p>Hai Sobat, Selamat datang di Katik!</p>
        <p>
          Sebuah web apps sederhana yang berfungsi untuk menghitung nilai luas
          dan keliling bangun datar
        </p>
      </div>
      <div class="button-click">
        <a href="#luas"><button>Luas persegi</button></a>
        <a href="#keliling"><button>Keliling Persegi</button></a>
      </div>
    </main>
    <!-- Kalkulator penghitung luas start -->
    <section>
      <div class="luas" id="luas">
        <div class="header-luas">
          <h2>Luas Persegi</h2>
        </div>
        <div class="kotak">
          <img src="../assets/persegi.png" alt="" />
          <p>bujur sangkar</p>
        </div>
        <div class="text">
          <h4>Rumus luas persegi:</h4>
          <p>dimana:</p>
          <p>L = S x S</p>
          <p>L = Luas</p>
          <p>S = Sisi</p>
        </div>
        <div class="nilai-luas">
          <h3>Hitung Luas Persegi</h3>
          <!-- Fungsi start -->
          <input
            id="sisi-luas"
            name="sisi-luas"
            type="number"
            placeholder="Nilai Sisi"
            required
          />

          <div id="hasilLuas" class="hasil-luas">
            <p class="result-luas" id="rumus-luas">L = S x S</p>
            <p class="result-luas" id="perhitungan-luas"></p>
            <p class="result-luas2">
              L = <span id="output-luas"></span> x
              <span id="output-luas2"></span>
            </p>
            <p class="result-luas2"><span id="result-akhir"></span></p>
            <p class="result-luas">L = <span id="result"></span> m&sup2;</p>
          </div>
          <span class="hitung">
            <button
              class="btn-hitung"
              id="btn-hitung"
              name="hitung-luas"
              @click="hitungLuas"
            >
              Hitung
            </button>
          </span>
          <span class="reset">
            <button
              class="btn-reset"
              id="btn-reset"
              name="reset-luas"
              @click="resetLuas"
            >
              Reset
            </button>
          </span>
          <!-- Fungsi end -->
        </div>
      </div>
    </section>
    <!-- Kalkulator penghitung luas end -->
    <!-- Kalkulator penghitung Keliling start -->
    <section>
      <div class="keliling" id="keliling">
        <div class="header-keliling">
          <h2>Keliling Persegi</h2>
        </div>
        <div class="kotak">
          <img src="../assets/persegi.png" alt="" />
          <p>bujur sangkar</p>
        </div>
        <div class="text">
          <h4>Rumus Keliling persegi:</h4>
          <p>dimana:</p>
          <p>K = 4 x S</p>
          <p>K = Keliling</p>
          <p>S = Sisi</p>
        </div>
        <div class="nilai-keliling">
          <h3>Hitung Keliling Persegi</h3>
          <!-- Fungsi start -->
          <p>
            <input
              id="sisi-keliling"
              type="number"
              name="sisi-keliling"
              placeholder="Nilai Sisi"
              required
              @input="validateNumericInput"
            />
          </p>
          <div id="output-keliling" name="output-keliling" class="hasil">
            <p class="text-left">
              K = 4 x S<br />
              K = <span id="rumus-keliling"></span><br />
              K = <span id="result-keliling"></span> m&sup2;
            </p>
          </div>
          <div class="hitung">
            <button
              name="hitung-keliling"
              @click="hitungKeliling"
              class="btn-hitung"
            >
              Hitung
            </button>
          </div>
          <div class="reset">
            <button
              type="reset"
              name="reset-keliling"
              @click="resetKeliling(0)"
              class="btn-reset"
            >
              Reset
            </button>
          </div>
        </div>
      </div>
    </section>

  </div>
</template>

<script>
export default{

  data() {
    return {
      sisiLuas: '',
      hasilLuas: '', 
      sisiKeliling: '',
      hasilKeliling: '' 
    };
  },

  methods: {
    hitungLuas() {
      if (!this.isValidNumericInput(this.sisiLuas)) {
        console.error('Masukkan angka yang valid untuk sisi luas.');
        return;
      }

      this.hasilLuas = `Luas Persegi: ${Math.pow(parseFloat(this.sisiLuas), 2)} m²`;
    },
    resetLuas() {
      this.sisiLuas = '';
      this.hasilLuas = '';
    },
    hitungKeliling() {
      if (!this.isValidNumericInput(this.sisiKeliling)) {
        console.error('Masukkan angka yang valid untuk sisi keliling.');
        return;
      }

      this.hasilKeliling = `Keliling Persegi: ${4 * parseFloat(this.sisiKeliling)} m`;
    },
    resetKeliling() {
      this.sisiKeliling = '';
      this.hasilKeliling = '';
    },
    isValidNumericInput(value) {
      return !isNaN(parseFloat(value)) && isFinite(value);
    }
  }
};

</script>


<style>
@import "../assets/style/style.css";
</style>
